.job {
    flex: 450px;
    background-color: #181A1A;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border: black solid 1px;
    flex-wrap: wrap;


}

.icon {
    height: 30vh;
    width: 100%;
    background-color: antiquewhite;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projectTitle {
    font-family: 'Josefin Sans', sans-serif;
    letter-spacing: 1px;
    line-height: 1.2;
    margin-bottom: 15px;
    font-size: 18px;
    color: red;
}

.description {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.8;
    letter-spacing: 1px;
    margin: 0 0 5px;
    font-weight: 400;
    color: antiquewhite;
}